<template>
  <div class="flux-container">
    <!-- Header flottant -->
    <div class="header">
      <h1> Carte des Flux de Documents</h1>
      <p>Visualisation en temps rel des changes entre services</p>
    </div>

    <!-- Panneau statistiques (droite) -->
    <div class="stats-panel">
      <h3> Statistiques</h3>
      <div class="stat-item">
        <span class="stat-label">Total flux:</span>
        <span class="stat-value">{{ stats.totalFlows }}</span>
      </div>
      <div class="stat-item">
        <span class="stat-label">Services:</span>
        <span class="stat-value">{{ stats.totalServices }}</span>
      </div>
      <div class="stat-item">
        <span class="stat-label">Connexions:</span>
        <span class="stat-value">{{ stats.totalConnections }}</span>
      </div>
      <div class="stat-item">
        <span class="stat-label">Volume total:</span>
        <span class="stat-value">{{ formatSize(stats.totalSize) }}</span>
      </div>
    </div>

    <!-- Panneau utilisateurs en ligne (gauche) -->
    <div class="online-users">
      <h3>
        <span class="online-indicator"></span>
        Utilisateurs en ligne (<span>{{ usersOnline.length }}</span>)
      </h3>
      <div class="users-list-container">
        <div v-for="user in usersOnline" :key="user.id" class="user-item">
          <div class="user-avatar">{{ user.initials }}</div>
          <div>
            <div class="user-name">{{ user.name }}</div>
            <div class="user-service-name">{{ user.service }}</div>
          </div>
        </div>
      </div>
      
      <div class="activity-feed">
        <h4> Activits rcentes</h4>
        <div class="activities-list-container">
          <div v-if="recentActivities.length === 0" class="no-activity">
            Aucune activit rcente
          </div>
          <div v-else v-for="activity in recentActivities" :key="activity.id" class="activity-item">
            <strong>{{ activity.sender }}</strong>  <strong>{{ activity.receiver }}</strong>
            <br>
             {{ activity.document }}
            <br>
            <span class="time-ago">{{ activity.timeAgo }}</span>
          </div>
        </div>
      </div>
    </div>

    <!-- Lgende (bas gauche) -->
    <div class="legend">
      <h4>Lgende</h4>
      <div class="legend-item">
        <div class="legend-color" style="background: #667eea;"></div>
        <span>Service</span>
      </div>
      <div class="legend-item">
        <div class="legend-color" style="background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);"></div>
        <span>Haute activit</span>
      </div>
      <div class="legend-item">
        <span style="margin-left: 30px; font-size: 12px; color: #666;">
           Survolez les nuds et liens pour plus d'infos
        </span>
      </div>
    </div>

    <!-- Loading -->
    <div v-if="isLoading" class="loading">
      <div class="spinner"></div>
      <p>Chargement des donnes...</p>
    </div>

    <!-- SVG Network Graph (plein cran) -->
    <svg ref="networkSvg" class="network-svg"></svg>

    <!-- Tooltip -->
    <div ref="tooltip" class="tooltip-box"></div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted, onUnmounted, nextTick } from 'vue';
import apiClient from '@/services/api';
import * as d3 from 'd3';

interface Node extends d3.SimulationNodeDatum {
  id: string;
  label: string;
  type: string;
  size: number;
  activity?: number;
  service_id?: string;
}

interface Link extends d3.SimulationLinkDatum<Node> {
  source: string | Node;
  target: string | Node;
  value: number;
  width: number;
  total_size?: number;
  types?: string[];
}

export default defineComponent({
  name: 'FluxDonneesView',
  setup() {
    const networkSvg = ref<SVGSVGElement | null>(null);
    const tooltip = ref<HTMLElement | null>(null);
    const usersOnline = ref<any[]>([]);
    const recentActivities = ref<any[]>([]);
    const isLoading = ref(true);
    const stats = ref({
      totalFlows: 0,
      totalServices: 0,
      totalConnections: 0,
      totalSize: 0
    });

    let simulation: d3.Simulation<Node, Link> | null = null;
    let refreshInterval: any = null;

    // Fonctions de drag pour D3.js
    const dragstarted = (event: any, d: Node) => {
      if (!event.active && simulation) simulation.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y;
    };

    const dragged = (event: any, d: Node) => {
      d.fx = event.x;
      d.fy = event.y;
    };

    const dragended = (event: any, d: Node) => {
      if (!event.active && simulation) simulation.alphaTarget(0);
      d.fx = null;
      d.fy = null;
    };

    // Fonction pour formater la taille
    const formatSize = (bytes: number) => {
      if (bytes === 0) return '0 B';
      const k = 1024;
      const sizes = ['B', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
    };

    // Fonction pour obtenir les initiales
    const getInitials = (name: string) => {
      return name.split(' ').map(n => n[0]).join('').toUpperCase().substring(0, 2);
    };

    // Fonction pour calculer le temps relatif
    const getTimeAgo = (date: string) => {
      const now = new Date();
      const past = new Date(date);
      const diffMs = now.getTime() - past.getTime();
      const diffMins = Math.floor(diffMs / 60000);
      
      if (diffMins < 1) return ' l\'instant';
      if (diffMins === 1) return 'Il y a 1 minute';
      if (diffMins < 60) return `Il y a ${diffMins} minutes`;
      const diffHours = Math.floor(diffMins / 60);
      if (diffHours === 1) return 'Il y a 1 heure';
      return `Il y a ${diffHours} heures`;
    };

    // Crer le graphe D3.js
    const createNetworkGraph = async () => {
      try {
        console.log(' Chargement des donnes de flux...');
        const response = await apiClient.get('/statistiques/flux-documents');
        console.log(' Donnes de flux reues:', response.data);

        if (!networkSvg.value) return;

        const width = window.innerWidth;
        const height = window.innerHeight;

        // Nettoyer le SVG existant
        d3.select(networkSvg.value).selectAll('*').remove();

        const svg = d3.select(networkSvg.value)
          .attr('width', width)
          .attr('height', height);

        let nodes: Node[] = [];
        let links: Link[] = [];

        if (response.data && response.data.data) {
          const data = response.data.data;

          // Mettre  jour les statistiques
          if (data.stats) {
            stats.value = {
              totalFlows: data.stats.total_flows || 0,
              totalServices: data.stats.total_services || 0,
              totalConnections: data.stats.total_connections || 0,
              totalSize: data.stats.total_size || 0
            };
          }

          // Crer les nuds
          if (data.nodes && Array.isArray(data.nodes)) {
            nodes = data.nodes.map((node: any) => ({
              id: node.id,
              label: node.label || node.name || node.nom,
              type: node.type || 'service',
              size: node.size || 20,
              activity: node.activity || 0,
              service_id: node.service_id || node.id
            }));
          }

          // Crer les liens
          if (data.links && Array.isArray(data.links)) {
            links = data.links.map((link: any) => ({
              source: link.source,
              target: link.target,
              value: link.value || 1,
              width: link.width || Math.sqrt(link.value || 1) * 2,
              total_size: link.total_size || 0,
              types: link.types || []
            }));
          }
        }

        // Si pas de donnes, crer des donnes de dmonstration
        if (nodes.length === 0) {
          nodes = [
            { id: '1', label: 'Comptabilit', type: 'service', size: 30, activity: 15 },
            { id: '2', label: 'RH', type: 'service', size: 25, activity: 10 },
            { id: '3', label: 'IT', type: 'service', size: 35, activity: 20 },
            { id: '4', label: 'Marketing', type: 'service', size: 28, activity: 8 },
            { id: '5', label: 'Logistique', type: 'service', size: 32, activity: 12 }
          ];

          links = [
            { source: '1', target: '2', value: 5, width: 4 },
            { source: '1', target: '3', value: 3, width: 3 },
            { source: '2', target: '3', value: 4, width: 3.5 },
            { source: '2', target: '4', value: 6, width: 4.5 },
            { source: '3', target: '4', value: 2, width: 2.5 },
            { source: '3', target: '5', value: 7, width: 5 },
            { source: '4', target: '5', value: 4, width: 3.5 },
            { source: '5', target: '1', value: 3, width: 3 }
          ];

          stats.value = {
            totalFlows: 8,
            totalServices: 5,
            totalConnections: 8,
            totalSize: 1024000
          };
        }

        // Crer la simulation
        simulation = d3.forceSimulation<Node>(nodes)
          .force('link', d3.forceLink<Node, Link>(links).id(d => d.id).distance(150))
          .force('charge', d3.forceManyBody().strength(-300))
          .force('center', d3.forceCenter(width / 2, height / 2))
          .force('collision', d3.forceCollide<Node>().radius((d: Node) => d.size + 10));

        // Crer les flches
        svg.append('defs').selectAll('marker')
          .data(['end'])
          .join('marker')
          .attr('id', 'arrow')
          .attr('viewBox', '0 -5 10 10')
          .attr('refX', 25)
          .attr('refY', 0)
          .attr('markerWidth', 6)
          .attr('markerHeight', 6)
          .attr('orient', 'auto')
          .append('path')
          .attr('d', 'M0,-5L10,0L0,5')
          .attr('fill', '#999');

        // Crer les liens
        const link = svg.append('g')
          .selectAll('path')
          .data(links)
          .join('path')
          .attr('class', 'link')
          .attr('stroke', '#999')
          .attr('stroke-width', (d: Link) => d.width)
          .attr('fill', 'none')
          .attr('stroke-opacity', 0.6)
          .attr('marker-end', 'url(#arrow)')
          .on('mouseover', function(event: any, d: Link) {
            const sourceNode = nodes.find(n => n.id === (d.source as Node).id);
            const targetNode = nodes.find(n => n.id === (d.target as Node).id);
            if (tooltip.value && sourceNode && targetNode) {
              d3.select(tooltip.value)
                .style('opacity', '1')
                .html(`
                  <strong>${sourceNode.label}  ${targetNode.label}</strong><br>
                  Documents: ${d.value}<br>
                  Taille: ${formatSize(d.total_size || 0)}
                `)
                .style('left', (event.pageX + 10) + 'px')
                .style('top', (event.pageY - 10) + 'px');
            }
          })
          .on('mouseout', function() {
            if (tooltip.value) {
              d3.select(tooltip.value).style('opacity', '0');
            }
          });

        // Crer les nuds
        const node = svg.append('g')
          .selectAll('circle')
          .data(nodes)
          .join('circle')
          .attr('class', 'node')
          .attr('r', (d: Node) => d.size)
          .attr('fill', (d: Node) => {
            const activity = d.activity || 0;
            if (activity > 10) return '#f5576c';
            if (activity > 5) return '#f093fb';
            return '#667eea';
          })
          .attr('stroke', '#fff')
          .attr('stroke-width', 2)
          .style('cursor', 'pointer')
          .call(d3.drag<SVGCircleElement, Node>()
            .on('start', dragstarted)
            .on('drag', dragged)
            .on('end', dragended) as any)
          .on('mouseover', function(event: any, d: Node) {
            d3.select(this).attr('stroke-width', 3);
            if (tooltip.value) {
              d3.select(tooltip.value)
                .style('opacity', '1')
                .html(`
                  <strong>${d.label}</strong><br>
                  Type: ${d.type}<br>
                  Activit: ${d.activity || 0} documents
                `)
                .style('left', (event.pageX + 10) + 'px')
                .style('top', (event.pageY - 10) + 'px');
            }
          })
          .on('mouseout', function() {
            d3.select(this).attr('stroke-width', 2);
            if (tooltip.value) {
              d3.select(tooltip.value).style('opacity', '0');
            }
          });

        // Ajouter les labels
        const label = svg.append('g')
          .selectAll('text')
          .data(nodes)
          .join('text')
          .attr('class', 'node-label')
          .text((d: Node) => d.label)
          .attr('text-anchor', 'middle')
          .attr('font-size', '12px')
          .attr('font-weight', 'bold')
          .attr('fill', 'white')
          .attr('text-shadow', '1px 1px 2px rgba(0,0,0,0.5)')
          .style('pointer-events', 'none');

        // Mise  jour de la simulation
        simulation.on('tick', () => {
          link.attr('d', (d: any) => {
            const dx = d.target.x - d.source.x;
            const dy = d.target.y - d.source.y;
            const dr = Math.sqrt(dx * dx + dy * dy);
            return `M${d.source.x},${d.source.y}A${dr},${dr} 0 0,1 ${d.target.x},${d.target.y}`;
          });

          node
            .attr('cx', (d: Node) => d.x!)
            .attr('cy', (d: Node) => d.y!);

          label
            .attr('x', (d: Node) => d.x!)
            .attr('y', (d: Node) => d.y! + 5);
        });

        isLoading.value = false;

      } catch (error) {
        console.error(' Erreur lors de la cration du graphe:', error);
        isLoading.value = false;
      }
    };

    // Charger les utilisateurs actifs
    const loadActiveUsers = async () => {
      try {
        console.log(' Chargement des utilisateurs actifs...');
        const response = await apiClient.get('/statistiques/utilisateurs-actifs');
        console.log(' Utilisateurs actifs reus:', response.data);

        if (response.data && response.data.data) {
          const data = response.data.data;

          // Utilisateurs
          if (data.active_users && Array.isArray(data.active_users)) {
            usersOnline.value = data.active_users.map((user: any, index: number) => ({
              id: index,
              name: user.name || user.user_name,
              service: user.service_nom || user.service,
              initials: getInitials(user.name || user.user_name || 'U')
            }));
          } else if (Array.isArray(data)) {
            usersOnline.value = data.map((user: any, index: number) => ({
              id: index,
              name: user.name || user.user_name,
              service: user.service_nom || user.service,
              initials: getInitials(user.name || user.user_name || 'U')
            }));
          }

          // Activits rcentes
          if (data.recent_activities && Array.isArray(data.recent_activities)) {
            recentActivities.value = data.recent_activities.slice(0, 5).map((activity: any, index: number) => ({
              id: index,
              sender: activity.from_user?.name || activity.sender_name || 'Expditeur',
              receiver: activity.to_user?.name || activity.receiver_name || 'Destinataire',
              document: activity.document_nom || activity.document_name || 'Document',
              timeAgo: activity.time_ago || getTimeAgo(activity.created_at || new Date().toISOString())
            }));
          } else if (data.recent_shares && Array.isArray(data.recent_shares)) {
            recentActivities.value = data.recent_shares.slice(0, 5).map((share: any, index: number) => ({
              id: index,
              sender: share.sender_name || 'Expditeur',
              receiver: share.receiver_name || 'Destinataire',
              document: share.document_name || 'Document',
              timeAgo: getTimeAgo(share.shared_at || new Date().toISOString())
            }));
          }
        }
      } catch (error) {
        console.error(' Erreur chargement utilisateurs:', error);
      }
    };

    // Lifecycle
    onMounted(async () => {
      await nextTick();
      await Promise.all([
        createNetworkGraph(),
        loadActiveUsers()
      ]);

      // Rafrachir toutes les 10 secondes
      refreshInterval = setInterval(() => {
        loadActiveUsers();
      }, 10000);
    });

    onUnmounted(() => {
      if (simulation) {
        simulation.stop();
      }
      if (refreshInterval) {
        clearInterval(refreshInterval);
      }
    });

    return {
      networkSvg,
      tooltip,
      usersOnline,
      recentActivities,
      isLoading,
      stats,
      formatSize
    };
  }
});
</script>

